<template>
  <input class="hide_file" ref="upload" type="file" @change="getFile($event)" accept="image/*">
  <div>
    <img :src="img.base64" class="img">
    <div class="buttons">
      <button class="button" v-on:click=select>选择图片</button>
      <button class="button" v-on:click="upload">上传数据</button>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: "CollectAvatar",
  data() {
    return {
      img: {
        file: "default",
        base64: "data:image/svg+xml;base64,"
      }
    }
  },
  methods: {
    getFile(e) {
      console.log(e.target.files[0])
      const file = e.target.files[0];
      const reader = new FileReader();
      const temp = this
      reader.onloadend = function () {
        temp.img = {file: e.target.files[0], base64: this.result}
      }
      reader.readAsDataURL(file);
    },
    select() {
      this.$refs.upload.click();
    },
    upload() {
      const formData = new FormData()
      formData.append('avatar', this.img.file)
      let config = {
        headers: {'Content-Type': 'multipart/form-data'}
      }
      axios.post("http://127.0.0.1:30001/upload", formData, config)
          .then(response => {
            alert(response.data["data"])
          }).catch(error => {
        alert('上传失败' + error)
        console.log(error)
      })
    }
  }
}

</script>


<style scoped>
.hide_file {
  display: none;
}

.img {
  width: 70%;
  margin: 0 auto;
}

.buttons {
  padding-top: 4%;
}

.button {
  width: 40%;
}
</style>